export async function CreateCard(idNum: string){
	const id : number = parseInt(idNum);
	let idString :string = idNum.toString();
	if (id >= 1 && id < 10){
		idString = `#000${idString}`;
	} else if (id >= 10 && id < 100){
		idString = `#00${idString}`;
	} else if (id >= 100 && id < 1000){
		idString = `#0${idString}`;
	} else {
		idString = `#${idString}`;
	}

	const data = await fetch(`https://pokeapi.co/api/v2/pokemon/${id.toString()}`)
	const posts = await data.json()
	const imgUrl :string = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${id.toString()}.png`;


	return (<Card className="w-fit h-fit m-1">
		<CardHeader className="flex items-center justify-center">
		<img src={imgUrl} alt="Bulbersor" />
		</CardHeader>
		<CardContent>
		<CardTitle>{posts.name}</CardTitle>
		<CardDescription>{idString}</CardDescription>
		</CardContent>
		<CardFooter className="flex items-start justify-center">
		{ posts &&
			posts.types.map((item, i) => (
				<Button className = "m-1" key={i}>{item.type.name}</Button>
			))
		}
		</CardFooter>
	</Card>);
}

export function CreateCardGrid(){
	return (
		<div className="grid grid-cols-6 grid-rows-3 gap-0 gap-y-0">
			<div className="col-start-2 row-start-1 col-span-4 flex items-center justify-between flex-wrap">
				{CreateCard("1")}
				{CreateCard("2")}
				{CreateCard("3")}
				{CreateCard("4")}
			</div>
			<div className="col-start-2 row-start-2 col-span-4 flex items-center justify-between flex-wrap ">
				{CreateCard("5")}
				{CreateCard("6")}
				{CreateCard("7")}
				{CreateCard("8")}
			</div>

			<div className="col-start-2 row-start-3 col-span-4 flex items-center justify-between flex-wrap">
				{CreateCard("9")}
				{CreateCard("10")}
				{CreateCard("11")}
				{CreateCard("12")}
			</div>
		</div>
	);
}

